* {box-sizing:border-box}

.text {
    color: #f2f2f2;
    
    font-size: 15px;
    padding: 8px 12px;
    position: absolute;
    bottom: 19px;
    width: 100%;
    text-align: center;
}

.fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
  }
  
  @-webkit-keyframes fade {
    from {opacity: .4}
    to {opacity: 1}
  }
  
  @keyframes fade {
    from {opacity: .4}
    to {opacity: 1}
}
.prev, .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    margin-top: -22px;
    padding: 16px;
    color: white;
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
  }

  .next {
    right: 0;
    border-radius: 3px 0 0 3px;
  }

  .prev:hover, .next:hover {
    background-color: rgba(0,0,0,0.8);
  }
.mySlides {
  width: 80vw;
  height: 80vw;
}
.landmarkPhoto {
    max-width: 900px;
    overflow: hidden;
    margin: 0 auto;

}
@media only screen and (max-width: 1000px) {
    .landmarkPhoto {
        width: 80vw;
    }
}