#loadingScreen {
    background: white;
    position: fixed;
    top: 0;
    left: 0;
    overflow: auto;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: none;
}
p {
    font-size: 2rem;
    font-family: "Nunito";
    text-align: center;
    position: relative;
    top: 40%;
}
.loader {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #F88158; /* Blue */
    border-radius: 50%;
    width: 100px;
    height: 100px;
    animation: spin 2s linear infinite;
    position: relative;
    margin: 0 auto;
    top: 45%;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

  @media only screen and (max-width: 560px) {

    .loader {
        width: 15vw;
        height: 15vw;
    }
}