* {box-sizing:border-box}

.Landmarks-gallery {
    max-width: 1000px;
    position: relative;
    margin: auto;
    margin-top: 15%;
   
}

  .dot {
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 0 2%;
    background-color: #6D6968;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
    
  }
  .dotsContainer {
      display: flex;
      justify-content: center;
  }
  
  .active, .dot:hover {
    background-color: rgb(248,129,88,1);
  }

 
