.Country {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90vw;
    margin: 1% auto;
    background-color: #D1D0CE;
}
.Home-Button {
    max-width: 40%;
    text-decoration: none;
    font-size: 1.5rem;
    color: rgb(248,129,88,0.5);
}
.Home-Button:hover {
    color: rgb(248,129,88,1);
}
.Country-Heading {
    display: flex;
    align-items: center;
    justify-content: center; 
    margin-top: 4%;
}
.Country h2 {
    font-size: 2rem;
    font-weight: 600;
}
.Flag {      
    display: flex;
    justify-content: flex-end;
    margin: 1%;
}
.Flag img{
  max-width: 10vw;
  margin: 0 auto;
}

.Top-Info {
    display: flex;
    justify-content: center;
    font-size: 1.5rem;
    margin-top: 10%;
    background-color: #D1D0CE;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    width: 90%;
}
.Bottom-Info {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    font-size: 1.5rem;
    margin: 10% 0;
    background-color: #D1D0CE;
}
.Bottom-Info span {
    font-weight: bold;
}

.Weather-Icon {
    display: inline-block;
    max-width: 100px;
    margin: 1%;
    flex-grow: 1;
}
.Capital {
    width: 100%;
    text-align: center;
    margin: 5% auto;
    font-size: 90%;
}

.Weather {
    margin: 1%;
    flex-grow: 1;
    font-size: 2.5rem;
    color: white;
    width: 25%;
    text-align: right;
}
.Weather-Description {
    margin: 1%;
    flex-grow: 1;
    width: 25%;
    font-family: "Rubik";
    text-align: left;
}
.statistic {
    margin: 3%;
    width: 60%;
    text-align: center;
    font-size: 1.5rem;
}
#time {
    font-size: 2rem;
    font-family: 'DotGothic16';
    background-color: rgb(248,129,88,1);
    padding: 0 1%;
    border-radius: 5px;
}
.Capital-Text {
    font-weight: bold;
    padding: 0 1%;
    line-height: 1.5;
    
}
.stat-span {
    font-family: "Nunito";
    font-weight: bold;
    background-color: rgb(32,32,32,0.3);
    border-radius: 5px;
    padding: 0 1%;
    line-height: 1.5;
}
.continent {
    width: 100%;
    text-align: center;
    font-size: 1.5rem;
}